<template>
  <div class="mainIndex">
      <audio ref="audio" class="app_m" loop :src="url" type="mp3"></audio>
      <el-button @click="playMusic" class="audioLeft" type="warning"  round size="mini">play</el-button>
      <el-button @click="playNext" class="audioright" type="warning"  round size="mini">next</el-button>
      <el-row>
        <el-col :span="12">
            <div class="heartText">
                <pre id="heartTextCopy"></pre>
            </div>
            <pre id="heartText" style="display: none;">
                从第一眼见到你，我感觉我喜欢上了你。
                每当我在遇到你的时候，
                我都想对你说我喜欢你，可我没有勇气。
                只能靠这张纸来表示我对你的一见钟情。
                从那第一次见到你，你怡人的微笑，
                你迷人的眼睛给我留下了不可磨没的影响。
                我喜欢上你了，我感到不可思议。
                我想：这就叫做一见钟情吧！
                每当我看到你和别的男生交流说话的时候，
                我的心就微微作痛。
                我喜欢你，我多么想在你伤心流泪的时候对你说声：“别伤心了”。
                在你别欺负的时候，我多么想保护你，不让你受一点伤害。
                我喜欢你，我不奢求你能接受我，我只希望你不要忘记我。
                我喜欢你，你能答应做我的女朋友吗？
                我爱你！
            </pre>
        </el-col>
        <el-col :span="12">
        </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'componentspage3',
  data() {
    return {
      id: 0,
      change: true,
      url: '',
      temp: 0
    }
  },
  computed: {
    musicChange() {
      return this.url
    }
  },
  created() {
    // this.url = this.$store.state.music[0]
  },
  mounted() {
    this.loveTime = setInterval(() => {
      this.heat()
    }, 200)
  },
  methods: {
    playMusic() {
      if (this.change === true) {
        this.$refs.audio.play()
        this.change = false
      } else {
        this.$refs.audio.pause()
        this.change = true
      }
    },
    playNext() {
      this.change = true
      this.$refs.audio.pause()
      this.id = this.id + 1
      if (this.id >= this.$store.state.music.length) {
        this.id = 0
      }
      this.url = this.$store.state.music[this.id]
      this.$nextTick(() => {
        this.$refs.audio.play()
        this.change = false
      })
    },
    color() {
      var r = Math.floor(Math.random() * 256)
      var g = Math.floor(Math.random() * 256)
      var b = Math.floor(Math.random() * 256)
      return 'rgb(' + r + ',' + g + ',' + b + ')' // 所有方法的拼接都可以用ES6新特性`其他字符串{$变量名}`替换
    },
    heat() {
      let txt = document.getElementById('heartText').innerHTML
      let heartTextCopy = document.getElementById('heartTextCopy')
      if (this.temp > txt.length) {
        heartTextCopy.style.color = this.color()
      }
      this.temp = this.temp + 1
      heartTextCopy.style.color = this.color()
      heartTextCopy.innerHTML = txt.substring(0, this.temp)
    }
  }
}
</script>

<style lang="stylus" scoped>
.mainIndex
  width: 100%;height: 100%;position relative
  .app_m
    position: absolute;
  .audioLeft
    position: absolute;left: 10px;top:10px;z-index: 1000;
  .audioright
    position: absolute;left: 3px;top:50px;z-index: 1001;
  .love
    min-width:600px ;
    height: 100%;
    overflow: hidden;
    box-sizing: border-box;
    background: linear-gradient(to right, rgb(0, 245, 255), white) no-repeat;
    padding-top: 100px;
  #heartTextCopy
      width: 100%;
      font-size: 19px;
      font-family: "仿宋";
  #heartTextCopy
      width: 50%;
</style>
